// ** React Imports
import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import classnames from "classnames"

// ** Custom Components
import InputGroup from '@components/input-group'

// ** Third Party Components
import { Button, Col, Form, Row } from 'reactstrap'

// ** Store Actions
import { recoveryCategoryEditChange, updateRecoveryCategory } from '@store/RecoveryCategory/actions'

const RecoveryCategoryAccountTab = ({
  selectedRecoveryCategory
}) => {
  // ** States
  const [recoveryCategoryData, setRecoveryCategoryData] = useState(null)
  const { isLoading, isSubmitting, formErrors } = useSelector(state => state.recoveryCategories),
    dispatch = useDispatch()

  // ** Update recoveryCategory image on mount or change
  useEffect(() => {
    if (selectedRecoveryCategory !== null) {
      setRecoveryCategoryData(selectedRecoveryCategory)
    }
  }, [selectedRecoveryCategory])
  // ** Function to handle form submit
  const handleSubmit = event => {
    event.preventDefault()
    dispatch(updateRecoveryCategory(selectedRecoveryCategory._id))
  }

  const handleFormChange = (name, value) => {
    dispatch(recoveryCategoryEditChange(name, value))
  }

  return (
    <Row>
      <Col sm='12'>
        <Form onSubmit={handleSubmit}>
          <Row>
            <Col md='4' sm='12'>
              <InputGroup
                type={'text'}
                label={'Service Name'}
                error={formErrors['serviceName']}
                name={'serviceName'}
                placeholder={'Service Name'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedRecoveryCategory.serviceName}
                required={true}
                className={classnames({ 'is-invalid': formErrors['serviceName'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'text'}
                label={'Category'}
                error={formErrors['serviceCategory']}
                name={'serviceCategory'}
                placeholder={'Service Category'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedRecoveryCategory.serviceCategory}
                required={true}
                className={classnames({ 'is-invalid': formErrors['serviceCategory'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'text'}
                label={'Subcategory'}
                error={formErrors['serviceSubCategory']}
                name={'serviceSubCategory'}
                placeholder={'Subcategory'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedRecoveryCategory.serviceSubCategory}
                required={true}
                className={classnames({ 'is-invalid': formErrors['serviceSubCategory'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'text'}
                label={'Icon Url'}
                error={formErrors['serviceIconUrl']}
                name={'serviceIconUrl'}
                placeholder={'Icon Url'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedRecoveryCategory.serviceIconUrl}
                required={true}
                className={classnames({ 'is-invalid': formErrors['serviceIconUrl'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'number'}
                label={'Price'}
                error={formErrors['basePrice']}
                name={'basePrice'}
                placeholder={'Price'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedRecoveryCategory.basePrice}
                required={true}
                className={classnames({ 'is-invalid': formErrors['basePrice'] })}
              />
            </Col>
            <Col className='d-flex flex-sm-row flex-column mt-2' sm='12'>
              <Button.Ripple className='mb-1 mb-sm-0 mr-0 mr-sm-1' type='submit' color='primary'>
                Save Changes
              </Button.Ripple>
              <Button.Ripple color='secondary' outline>
                Reset
              </Button.Ripple>
            </Col>
          </Row>
        </Form>
      </Col>
    </Row>
  )
}

export default RecoveryCategoryAccountTab
