/*
 *
 * RecoveryCategory actions
 *
 */

import axios from 'axios'
import { success } from "react-notification-system-redux"

import {
  RESET_FORM,
  GET_ALL_DATA,
  GET_DATA,
  GET,
  ADD,
  DELETE,
  SET_LOADING,
  SET_SUBMITTING,
  SET_FORM_ERRORS,
  FORM_CHANGE,
  EDIT_CHANGE,
  DEACTIVATE
} from './constants'
import handleError from '@utils/error'
import { allFieldsValidation } from '@utils/validation'

export const recoveryCategoryFormChange = (name, value) => {
  const formData = {}
  formData[name] = value

  return {
    type: FORM_CHANGE,
    payload: formData
  }
}

export const recoveryCategoryEditChange = (name, value) => {
  const formData = {}
  formData[name] = value

  return {
    type: EDIT_CHANGE,
    payload: formData
  }
}

// ** Get all Data
export const getAllRecoveryCategories = () => {
  return async dispatch => {
    await axios.get('/api/recoveryCategories').then(response => {
      dispatch({
        type: GET_ALL_DATA,
        data: response.data.recoveryCategories
      })
    })
  }
}

// ** Get data on page or row change
export const getRecoveryCategoryList = params => {
  return async dispatch => {
    await axios.get('/api/recoveryCategories', { params }).then(response => {
      dispatch({
        type: GET_DATA,
        data: response.data.recoveryCategories,
        totalPages: response.data.total,
        params
      })
    })
  }
}

// ** Get RecoveryCategory
export const getRecoveryCategory = id => {
  return async dispatch => {
    await axios
      .get(`/api/recoveryCategories/${id}`)
      .then(response => {
        dispatch({
          type: GET,
          selectedRecoveryCategory: response.data.recoveryCategory
        })
      })
      .catch(err => console.log(err))
  }
}

// ** Add recoveryCategory
export const addRecoveryCategory = () => {
  return async (dispatch, getState) => {
    const rules = {
      serviceName: 'required',
      serviceCategory: 'required',
      serviceSubCategory: 'required',
      serviceIconUrl: 'required',
      basePrice: 'required'
    }

    const recoveryCategory = getState().recoveryCategories.recoveryCategoryFormData
    const { isValid, errors } = allFieldsValidation(recoveryCategory, rules, {
      'required.serviceName': 'Service name is required.',
      'required.serviceCategory': 'Category is required.',
      'required.serviceSubCategory': 'Subcategory is required.',
      'required.serviceIconUrl': 'Icon Url is required.',
      'required.basePrice': 'Base price is required.'
    })

    if (!isValid) {
      return dispatch({ type: SET_FORM_ERRORS, payload: errors })
    }

    dispatch({ type: SET_SUBMITTING, payload: true })
    dispatch({ type: SET_LOADING, payload: true })

    try {
      const response = await axios.post('/api/recoveryCategories', recoveryCategory)
      dispatch({
        type: ADD,
        recoveryCategory
      })
      dispatch({
        type: RESET_FORM,
        payload: {}
      })
      dispatch(getRecoveryCategoryList(getState().recoveryCategories.params))
      dispatch(getAllRecoveryCategories())

      const successfulOptions = {
        title: `The recoveryCategory has been added successfully!`,
        position: 'tr',
        autoDismiss: 1
      }

      dispatch(success(successfulOptions))
    } catch (error) {
      const title = `We were not able to create the recoveryCategory. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_SUBMITTING, payload: false })
      dispatch({ type: SET_LOADING, payload: false })
    }
  }
}

// ** Delete recoveryCategory
export const updateRecoveryCategory = (_id) => {
  return async (dispatch, getState) => {
    const rules = {
      serviceName: 'required',
      serviceCategory: 'required',
      serviceSubCategory: 'required',
      serviceIconUrl: 'required',
      basePrice: 'required'
    }

    const recoveryCategory = { ...getState().recoveryCategories.selectedRecoveryCategory }
    const { isValid, errors } = allFieldsValidation(recoveryCategory, rules, {
      'required.serviceName': 'Service name is required.',
      'required.serviceCategory': 'Category is required.',
      'required.serviceSubCategory': 'Subcategory is required.',
      'required.serviceIconUrl': 'Icon Url is required.',
      'required.basePrice': 'Base price is required.'
    })


    if (!isValid) {
      return dispatch({ type: SET_FORM_ERRORS, payload: errors })
    }

    dispatch({ type: SET_SUBMITTING, payload: true })
    dispatch({ type: SET_LOADING, payload: true })

    try {
      delete recoveryCategory._id
      delete recoveryCategory.createdAt
      delete recoveryCategory.updatedAt
      delete recoveryCategory.userInfo
      delete recoveryCategory.__v
      await axios.put(`/api/recoveryCategories/${_id}`, recoveryCategory)
      dispatch({
        type: RESET_FORM,
        payload: {}
      })

      const successfulOptions = {
        title: `RecoveryCategory information was updated successfully!`,
        position: 'tr',
        autoDismiss: 1
      }

      dispatch(success(successfulOptions))
    } catch (error) {
      const title = `We were not able to update the recoveryCategory. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_SUBMITTING, payload: false })
      dispatch({ type: SET_LOADING, payload: false })
    }
  }
}

// ** Delete recoveryCategory
export const deactivateRecoveryCategory = id => {
  return async (dispatch, getState) => {
    try {
      const response = await axios.post(`/api/recoveryCategories/${id}/deactivate`)
      if (response.data.success === true) {
        const successfulOptions = {
          title: `${response.data.message}`,
          position: 'tr',
          autoDismiss: 1
        }
        dispatch(success(successfulOptions))
        dispatch({
          type: DEACTIVATE
        })
      }
    } catch (error) {
      const title = `We were not able to delete the recoveryCategory. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_SUBMITTING, payload: false })
      dispatch({ type: SET_LOADING, payload: false })
    }
  }
}

// ** Delete recoveryCategory
export const deleteRecoveryCategory = id => {
  return async (dispatch, getState) => {
    try {
      const response = await axios.delete(`/api/recoveryCategories/${id}`)
      if (response.data.success === true) {
        const successfulOptions = {
          title: `${response.data.message}`,
          position: 'tr',
          autoDismiss: 1
        }
        dispatch(success(successfulOptions))
        dispatch({
          type: DELETE
        })
      }
    } catch (error) {
      const title = `We were not able to delete the recoveryCategory. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_SUBMITTING, payload: false })
      dispatch({ type: SET_LOADING, payload: false })
    }
  }
}
