// ** React Imports
import { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'

// ** UserCars Edit Components
import AccountTab from './Account'

// ** Store & Actions
import { getRecoveryCategory } from '@store/RecoveryCategory/actions'
import { useDispatch, useSelector } from 'react-redux'

// ** Third Party Components
import { Alert, Card, CardBody, Col, Row } from 'reactstrap'

// ** Custom Components
import UILoader from '@components/ui-loader'

// ** Styles
import '@styles/react/apps/app-users.scss'

const RecoveryCategoryEdit = () => {
  // ** States & Vars
  const [activeTab, setActiveTab] = useState('1'),
    { isLoading, selectedRecoveryCategory } = useSelector(state => state.recoveryCategories),
    dispatch = useDispatch(),
    { id } = useParams()

  // ** Function to toggle tabs
  const toggle = tab => setActiveTab(tab)

  // ** Function to get recoveryCategory on mount
  useEffect(() => {
    dispatch(getRecoveryCategory(id))
  }, [dispatch])

  return selectedRecoveryCategory !== null && selectedRecoveryCategory !== undefined ? (
    <UILoader blocking={isLoading}>
      <Row className='app-user-edit'>
        <Col sm='12'>
          <Card>
            <CardBody className='pt-2'>
              <AccountTab selectedRecoveryCategory={selectedRecoveryCategory} />
            </CardBody>
          </Card>
        </Col>
      </Row>
    </UILoader>
  ) : (
    <Alert color='danger'>
      <h4 className='alert-heading'>RecoveryCategory not found</h4>
      <div className='alert-body'>
        RecoveryCategory with id: {id} doesn't exist. Check list of all RecoveryCategories: <Link to='/recoveryCategory/list'>RecoveryCategories List</Link>
      </div>
    </Alert>
  )
}

export default RecoveryCategoryEdit
